@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Montserrat", sans-serif}.App .logo{position:absolute;width:48px;height:48px;left:28px;top:28px}.Page{display:flex;flex-direction:column;margin-top:142px;margin-left:137px;margin-right:137px}

.TextField{margin:20px 0 20px 0;display:flex;flex-direction:column;width:320px}.TextField input{width:320px;height:44px;left:141px;top:163px;background:#ffffff;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:12px}.TextField input:focus,.TextField input:hover{border:1px solid rgba(31,32,65,0.5);outline-width:0}.TextField>div{display:flex;justify-content:space-between;margin-bottom:5px}.TextField>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.TextField>div div:nth-child(2){font-size:12px;line-height:15px;font-weight:400;text-transform:uppercase;color:rgba(31,32,65,0.5)}

.DropDown{margin:20px 0 20px 0;display:flex;flex-direction:column;width:320px}.DropDown .Visitors{-webkit-user-select:none;-ms-user-select:none;user-select:none;display:flex;align-items:center;justify-content:space-between;width:320px;height:44px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:14px;padding-right:15px}.DropDown .Visitors:focus,.DropDown .Visitors:hover{border:1px solid rgba(31,32,65,0.5);outline-width:0}.DropDown>div{display:flex;justify-content:space-between;margin-bottom:4px}.DropDown>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.DropDown .guest-list{-webkit-user-select:none;-ms-user-select:none;user-select:none;height:159px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:10px;padding-right:10px;font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;color:#1f2041}.DropDown .guest-list .guest-list-adults{display:flex;justify-content:space-between;align-items:center}.DropDown .guest-list .guest-list-adults>div{display:flex;align-items:center}.DropDown .guest-list .guest-list-adults>div .minus,.DropDown .guest-list .guest-list-adults>div .plus{display:flex;align-items:center;justify-content:center;width:30px;height:30px;color:#1f2041;cursor:pointer;border:1px solid;border-color:#1f2041;opacity:0.25;border-radius:22px;margin:10px}.DropDown .guest-list .guest-list-adults>div .minus.clicked,.DropDown .guest-list .guest-list-adults>div .plus.clicked{color:#1f2041;border-color:#1f2041;opacity:0.5}

.Checkbox{width:320px;margin:20px 0 20px 0}.Checkbox>div{display:flex;justify-content:space-between;margin-bottom:5px}.Checkbox>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.check-list{display:flex;flex-direction:column}.check-list .checkbox{display:none}.check-list .checkbox+span{display:inline-flex;align-items:flex-start;width:240px;margin:5px 0 5px 0;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-size:14px;color:rgba(31,32,65,0.5)}.check-list .checkbox+span::before{content:"";margin-right:10px;width:20px;height:20px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;padding-right:15px}.check-list .checkbox+.checked::before{border:1px solid #bc9cff;box-sizing:border-box;border-radius:4px;background-image:url(/MetaLamp_Task_2/static/media/Vector.ad694aaf.svg);background-repeat:no-repeat;background-position:center}

.RadioButtons{width:320px;margin:20px 0 20px 0}.RadioButtons>div{display:flex;justify-content:space-between;margin-bottom:16px}.RadioButtons>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.radio-buttons{display:flex}.radio-buttons input{display:none}.radio-buttons span{font-weight:normal;color:rgba(31,32,65,0.75);margin-right:15px}.radio-buttons span::before{content:"";width:20px;height:20px;padding-right:20px;margin-right:10px;border-radius:50%;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box}.radio-buttons #men:checked+label span::before,.radio-buttons #women:checked+label span::before{border:1px solid #bc9cff;background-image:url(/MetaLamp_Task_2/static/media/circle.8b4bd132.svg);background-repeat:no-repeat;background-position:center}

.Subscription{margin:20px 0 20px 0;display:flex;flex-direction:column;width:320px}.Subscription input{width:267px;height:44px;left:141px;top:163px;background:#ffffff;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:12px}.Subscription input:focus,.Subscription input:hover{border:1px solid rgba(31,32,65,0.5);outline-width:0}.Subscription>div{display:flex;justify-content:space-between;margin-bottom:5px}.Subscription>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.arrow-button{position:absolute;padding:0;border:none;font:inherit;color:inherit;width:18px;height:18px;background-color:transparent;background-image:url(/MetaLamp_Task_2/static/media/arrow_forward.ae9e8d9b.svg);background-repeat:no-repeat;cursor:pointer;-webkit-transform:translate(-32px, 13px);transform:translate(-32px, 13px)}

.Toggle{position:relative}.Toggle>div{display:flex;justify-content:space-between;margin-bottom:5px}.Toggle>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.Toggle .checkbox input{display:none}.Toggle .checkbox span{display:inline-flex;align-items:flex-start;width:270px;margin:5px 0 5px 0;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-size:14px;color:rgba(31,32,65,0.5)}.Toggle .checkbox span:before{content:"";cursor:pointer;padding-right:40px;margin-right:10px;width:40px;height:20px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:10px}.Toggle .checkbox-circle{position:absolute;bottom:9px;left:4px;width:12px;height:12px;background:rgba(31,32,65,0.25);border-radius:10px}.Toggle .checkbox input:checked ~ span::before{border:1px solid #bc9cff}.Toggle .checkbox input:checked ~ span .checkbox-circle{-webkit-transform:translateX(22px);transform:translateX(22px);background:linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%)}.Toggle .checkbox *{transition:all 0.5s cubic-bezier(0.2, 0.85, 0.32, 1.2)}

.LikeButton{position:relative;margin:20px 0 20px 0}.LikeButton>div{display:flex;justify-content:space-between;margin-bottom:5px}.LikeButton>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.LikeButton .container{display:inline-block;position:relative;width:40px;height:20px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:10px;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.LikeButton .container span{position:absolute;top:3px;left:22px;font-style:normal;font-weight:normal;font-size:10px;line-height:12px;color:rgba(31,32,65,0.25)}.LikeButton .container span::before{content:"";position:absolute;top:2px;left:-17px;width:10px;height:8px;transition:0.5s ease-in-out;background-image:url(/MetaLamp_Task_2/static/media/heart.69d1f532.svg)}.LikeButton input{display:none}.LikeButton input:checked ~ .container{border:1px solid #bc9cff}.LikeButton input:checked ~ .container span{color:#bc9cff}.LikeButton input:checked ~ .container span::before{background-image:url(/MetaLamp_Task_2/static/media/heart-act.dabf545f.svg)}

.RateButton{position:relative;margin:20px 0 20px 0;width:120px}.RateButton>div{display:flex;justify-content:space-between;margin-bottom:5px}.RateButton>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.stars{-webkit-transform:rotateY(180deg) translatex(4px);transform:rotateY(180deg) translatex(4px)}.stars div{cursor:pointer;width:24px;height:24px;background-image:url(/MetaLamp_Task_2/static/media/star_border.d6f803db.svg);background-repeat:no-repeat}.stars .star-1:hover,.stars .star-2:hover,.stars .star-3:hover,.stars .star-4:hover,.stars .star-5:hover{background-image:url(/MetaLamp_Task_2/static/media/star.5a1fce0b.svg)}.stars .star-1:hover ~ *,.stars .star-2:hover ~ *,.stars .star-3:hover ~ *,.stars .star-4:hover ~ *,.stars .star-5:hover ~ *{background-image:url(/MetaLamp_Task_2/static/media/star.5a1fce0b.svg)}.stars .active{background-image:url(/MetaLamp_Task_2/static/media/star.5a1fce0b.svg)}

.Buttons{width:320px;margin:20px 0 20px 0}.Buttons>div{display:flex;justify-content:space-between;margin-bottom:5px}.Buttons>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.Buttons form{display:flex;flex-direction:column}.full,.border,.none,.go-to-pay{border-radius:22px;width:99px;border:none;cursor:pointer;font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;padding:0;height:44px;margin:10px 0 10px 0}.full:hover,.border:hover,.none:hover,.go-to-pay:hover{opacity:1}.full{color:#ffffff;background:linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);opacity:0.5}.border{background:white;border:2px solid;color:#bc9cff;border-color:rgba(187,155,255,0.5)}.border:hover{border-color:#bb9bff}.none{color:rgba(31,32,65,0.5);background:white}.none:hover{color:#bc9cff}.go-to-pay{width:320px;color:#ffffff;background:linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%)}.go-to-pay::after{content:"";position:absolute;width:18px;height:18px;right:22px;background-image:url(/MetaLamp_Task_2/static/media/arrow_forward_white.d4701dff.svg)}

